import midPackageIcon from './../../images/packages/mid-package-icon.svg'
import bigPackageIcon from './../../images/packages/big-package-icon.svg'
import xlPackageIcon from './../../images/packages/xl-package-icon.svg'

import chrome from './../../images/packages/icon-chrome.svg';
import film from './../../images/packages/icon-film.svg';
import microphone from './../../images/packages/icon-microphone.svg';
import music from './../../images/packages/icon-music.svg';
import twitch from './../../images/packages/icon-twitch.svg';

class InternetPackage {
  iconsList = [chrome, film, microphone, music, twitch];

  constructor(size, speed, price, features) {
    switch (size) {
      case 'mid':
        this.icon = midPackageIcon;
        this.icons = this.iconsList.slice(0, 2);
        this.title = "Среден";
        this.isPrimaryPackage = false;
        break;
      case 'big':
        this.icon = bigPackageIcon;
        this.icons = this.iconsList.slice(0, 3);
        this.title = "Голям";
        this.isPrimaryPackage = true;
        break;
      case 'xl':
        this.icon = xlPackageIcon;
        this.icons = this.iconsList.slice(0, 5);
        this.title = "Пълна програма";
        this.isPrimaryPackage = false;
        break;
      default:
        this.icon = bigPackageIcon;
        this.icons = this.iconsList.slice(0, 3);
        this.title = "Голям";
        this.isPrimaryPackage = false;
        break;
    }
    this.isOnlyInternet = true;
    this.internetSpeed = speed;

    this.originalPrice = parseFloat(price);

    this.packageFeatures = this.initFeatures(features);
    this.duration = features.duration;
    this.fetchedPackage = features;
  };

  initFeatures = (features) => {
    let result = [];

    result.push({ text: 'Бонус точки', active: features.bonuspoints === 'Да' })
    result.push({ text: ('Брой Паузи/Изразвания - ' + (features.pausescuts)), active: features.pausescuts !== 0 })
    result.push({ text: (features.discounts !== 'Не' ? features.discounts : 'Отстъпки'), active: features.discounts !== 'Не' })
    result.push({ text: 'Индивидуалност', active: features.individuallity !== 'Не' })
    result.push({ text: 'TimeShift', active: features.timeshift !== 'Не' })
    result.push({ text: 'Архив', active: features.archive !== 'Не' })
    result.push({ text: 'Запис', active: features.record !== 'Не' })

    return result.sort(this.sortPackages);
  }

  // Sort packages to show the true values first
  sortPackages = (p1, p2) => {
    return p1.active === p2.active ? 0 : p1.active ? -1 : 1;
  }

  getPriceAccordingToDuration = () => {
    switch(parseInt(this.duration)) {
      case 0:
        return this.originalPrice * 1.5;
      case 1:
        return this.originalPrice * 1.2;
      default:
        return this.originalPrice;
    }
  }

  getPriceWithExtras = () => this.getPriceAccordingToDuration();

  getPresentationPrice = () => String(this.getPriceWithExtras()).split('.');

  getPriceMain = () => this.getPresentationPrice()[0];
  getPriceCoins = () => {
    return this.getPresentationPrice()[1].slice(0, 2);
    // return 99;
  };
}


export default InternetPackage;