import React, { useReducer } from 'react';
import { CheckoutHeader } from './CheckoutHeader';
import { StepWrapper } from './StepWrapper';
import packages from '../../constants/packages';

const reducer = (state, action) => {
  switch (action.type) {
    case 'field': {
      let newPackage = state.packageInfo;
      newPackage[action.objField] = action.value === '' ? 1 : action.value;
      return {
        ...state,
        [action.field]: action.value,
        packageInfo: newPackage,
      }
    }

    case 'add_extra': {
      let newPackage = state.packageInfo;
      newPackage.setExtraAdded(action.index, action.isAdded);

      return {
        ...state,
        packageInfo: newPackage,
      }
    }
    case 'next_step': {
      return {
        ...state,
        currentStep: state.currentStep + 1
      }
    }
    default: {
      return state;
    }
  }
}

const initialState = {
  tvCount: '',
  currentStep: 0,
  packageInfo: packages.bigPackage,
  orderNumber: Date.now().toString().slice(0, 6) + makeID(3),
}

const Checkout = ({ data }) => {
  // const data = useStaticQuery(graphql`
  //   query Checkout {
  //     step1: file(relativePath: {eq: "checkout/checkout-step-1.png"}) {
  //       childImageSharp {
  //         fluid(maxWidth: 500) {
  //           src
  //         }
  //       }
  //     }
  //   }
  // `);

  const [state, dispatch] = useReducer(reducer, initialState);
  const { currentStep } = state;

  return (
    <>
      <div className='row w-100 d-flex justify-content-center' style={{ paddingTop: '12rem' }}>
        <div className='col-10'>
          <CheckoutHeader linkBackTo={'/za-doma'} step={currentStep} />

          <StepWrapper
            title={'Персонализирай своя NetSurf пакет'}
            dispatch={dispatch}
            parentState={state}
          />
        </div>
      </div>
    </>
  )
}

export default Checkout;

function makeID(length) {
  var result = '';
  var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
  var charactersLength = characters.length;
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}