import InternetPackage from './InternetPackage';
import diemaExtraIcon from '../../images/checkout/extra-1.png';
import movieStarIcon from '../../images/checkout/extra-2.png';

class TvPackage extends InternetPackage {
  constructor(size, speed, price, features, title, hdFetcherInfo, tvCount) {
    super(size, speed, price, features);
    this.title = title;
    this.tvChannels = title.split(" ")[0] + "+";
    this.hdChannels = Math.floor(parseInt(title.split(" ")[0]) / 3) + "+";
    this.hdFetcher = tvCount;
    this.hdFetcherInfo = hdFetcherInfo;
    this.isOnlyInternet = false;

    this.tvExtras = [
      { extra: new TvExtra('Diema Extra', 'Стани част от спортната емоция с каналите от пакет Diema Extra!', diemaExtraIcon, 8.99), isAdded: false },
      { extra: new TvExtra('MovieSTAR', 'Само тук можете да гледате любимите си филми, без досадни рекламни, прекъсвания и в оригиналната им езикова версия.', movieStarIcon, 2.99), isAdded: false },
    ];
  }

  setExtraAdded = (index, isAdd) => {
    this.tvExtras[index] = { ...this.tvExtras[index], isAdded: isAdd };
  }

  setTvCount = (tvCount) => this.hdFetcher = tvCount;

  getPriceWithExtras = () => {
    return (this.getPriceAccordingToDuration() + this.getAdditionalTVsPrice(this.hdFetcher) + this.getExtrasPrice());
  }

  getAdditionalTVsPrice = (numberOfTvs) => {
    const tvNum = parseInt(numberOfTvs);
    switch (tvNum) {
      case 1:
        return 0;
      case 2:
      case 3:
        return (tvNum - 1) * 2.5;
      default:
        return (tvNum - 3) * 5 + 5;
    }
  }

  getExtrasPrice = () => {
    let res = 0;
    this.tvExtras.forEach((v) => {
      if (v.isAdded) res += v.extra.price;
    });
    return res;
  };
}

class TvExtra {
  constructor(name, description, icon, price) {
    this.name = name;
    this.description = description;
    this.icon = icon;
    this.price = price;
  }
}

Object.setPrototypeOf(TvPackage.prototype, InternetPackage);

export default TvPackage;